//
// c3.scss
//
 
.c3-tooltip {
    box-shadow: $box-shadow-lg;
    opacity: 1;
    td {
        border-left: none;
        font-family: $font-family-secondary;
        >span {
            background: $dark;
            
        }
    }
    tr {
        border: none !important;
    }    
    th {
        background-color: $dark;
        color: $gray-100;
    }
}

.c3-chart-arcs-title {
    font-size: 18px;
    font-weight: 600;
}

.c3 {
    text {
        font-family: $font-family-base;
        fill: $gray-600;
    }
    line, path{
        stroke: $gray-400;
    }
}


.c3-chart-arc.c3-target {
    g {
        path {
            stroke: $white;
        }
    }
}